uentra el paciente. Las fases del cambio son las siguientes: ")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-12 order-2 order-lg-1",attrs:{"data-aos":"fade-left"}},[_c('TabsA',{staticClass:"color-acento-contenido mb-5"},[_c('div',{staticClass:"tarjeta clr-paleCyan p-4",attrs:{"titulo":"1. Precontemplación"}},[_c('h4',[_vm._v("Precontemplación")]),_c('p',[_vm._v("El individuo no ha pensado que tiene un problema ni que necesita realizar un cambio en su vida.")])]),_c('div',{staticClass:"tarjeta clr-paleCyan p-4",attrs:{"titulo":"2. Contemplación"}},[_c('h4',[_vm._v("Contemplación")]),_c('p',[_vm._v("El individuo empieza a pensar que tiene un problema, y entonces aparece la ambivalencia. Considera la posibilidad del cambio, a la vez que lo rechaza.")])]),_c('div',{staticClass:"tarjeta clr-paleCyan p-4",attrs:{"titulo":"3. Determinación "}},[_c('h4',[_vm._v("Determinación ")]),_c('p',[_vm._v("El individuo toma la decisión de cambiar y entra directamente en la siguiente etapa, de acción.")])]),_c('div',{staticClass:"tarjeta clr-paleCyan p-4",attrs:{"titulo":"4. Acción"}},[_c('h4',[_vm._v("Acción")]),_c('p',[_vm._v("El individuo se compromete en acciones de responsabilidad que la llevan a introducir cambios en su conducta.")])]),_c('div',{staticClass:"tarjeta clr-paleCyan p-4",attrs:{"titulo":"5. Mantenimiento "}},[_c('h4',[_vm._v("Mantenimiento ")]),_c('p',[_vm._v("El reto consiste en alimentar el cambio conseguido y evitando la recaída.")])]),_c('div',{staticClass:"tarjeta clr-paleCyan p-4",attrs:{"titulo":"6. Recaídas"}},[_c('h4',[_vm._v("Recaídas")]),_c('p',[_vm._v("Se trata de ayudar al individuo evitando el desánimo y la desmoralización, renovando la determinación y prosiguiendo con las etapas de acción y mantenimiento.")])])])],1)]),_c('Separador'),_vm._m(10),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("La responsabilidad que tiene en el entorno clínico este modelo es clave pues le da un papel importante al reconocimiento del 